@import "./sass-variables.sass";

.projectDisactive {
    position: fixed;
    z-index: -100;
    opacity: 0; }
.projectActive {
    position: fixed;
    width: 100vw;
    height: 100vh;
    background-color: rgba($csecond, 0.3);
    display: grid;
    place-items: center;
    z-index: 100000;
    top: 0;
    left: 0;
    opacity: 1;
    transition: 0.5s;
    #projectContainer {
        width: 100%;
        height: 650px;
        display: flex;
        margin-top: 50px;
        justify-content: center;
        // align-items: center
        place-items: center;
        background: $lin2;
        #activeText {
            width: 300px;
            text-align: left;
            color: $clight;
            margin: 50px;
            h1 {
                margin-bottom: 30px; }
            ul {
                list-style: square;
                li {
                    font-size: 20px;
                    margin-bottom: 15px; } } }
        #activeImg {
            width: 720px;
            height: 540px;
            overflow: hidden;
            img {
                width: 720px;
                height: 540px; } } } }

@media(max-width: 1200px) {
    .projectActive {
        #projectContainer {
            height: 550px;
            margin-top: 0;
            #activeText {
                width: 200px;
                margin: 0;
                margin-right: 50px; }
            #activeImg {
                width: 600px;
                height: 450px;
                overflow: hidden;
                img {
                    width: 600px;
                    height: 450px; } } } } }

@media(max-width: 900px) {
    .projectActive {
        #projectContainer {
            height: 70%;
            flex-direction: column;
            align-items: center;
            #activeText {
                width: 80%;
                text-align: center;
                margin: 0;
                display: flex;
                flex-direction: column;
                align-items: center;
                margin-bottom: 30px;
                // border: 2px solid red
                ul {
                    display: flex;
                    flex-wrap: wrap;
                    text-align: left;
                    justify-content: center;
                    // border: 2px solid yellow
                    padding-left: 50px;
                    li {
                        width: 40%;
                        margin: 10px;
                        text-align: {}
 } } } } } }                        // border: 2px solid lawngreen
